import {
	api
} from '../../const'
import {
	passTime,
	debounce
} from '../../utils'
import sendReply from '../../components/sendReply'
import {
	checkT
} from '../../utils/net'
import login from '../../components/login'


const debounceOnScroll = () => debounce(function(e) {
	this.top = e.target.scrollTop
})
export default {
	components: {
		sendReply,
		login
	},
	mounted() {
		this.getData()
	},
	computed: {
		formatCreateAt() {
			return passTime(this.reply.createtime)
		}
	},
	onShow() {
		var that = this;
		var t = uni.getStorageSync("t")
		checkT(t,
			function() {
				that.loginVisible = true
			},
			function() {});
	},

	methods: {
		onScroll: debounceOnScroll(),
		async getData() {
			const t = uni.getStorageSync('t')
			uni.showLoading({
				title: '加载中'
			})
			this.$http({
				t: t,
				act: 'reply.detail',
				replyId: this.currentReplyId,
				size: 5,
				pageNum: 0
			},function(res){
				this.reply = res.data
				uni.hideLoading()
			})
			

		},

		goTop() {
			// console.log(11);
			this.top = 0
		},
		getMore() {
			// if (this.remainReplies.length > 0) {
			//   this.currentReplies.concat(this.remainReplies.splice(0, 10))
			// } else {
			//   wx.showToast({
			//     title: '无更多数据',
			//     icon: 'none',
			//     duration: 2000
			//   })
			// }
		},
		showReplyModal(e) {
			var userId = uni.getStorageSync('userId')

			const userName = e.currentTarget.dataset.username
			const replyId = e.currentTarget.dataset.replyid
			if (userName) {
				this.content = `@${userName}:`
			} else {
				this.content = ''
			}
			if (userId == this.postUserId) {
				this.isPostUserId = true
			}
			this.replyId = replyId
			this.replyUserName = userName
			this.sendVisible = true
		},
		replySuccess() {
			this.closeModal()
			uni.showToast({
				title: '评论成功',
				icon: 'none',
				duration: 1500

			})
			this.getData()
		},
		closeModal() {
			this.sendVisible = false
		}
	},
	onLoad() {
		this.currentReplyId = this.$root.$mp.query.replyId
		this.postId = this.$root.$mp.query.postId
		this.postAnonymous = this.$root.$mp.query.anonymous
		this.postUserId = this.$root.$mp.query.postUserId
		console.log("postUserId=" + this.postUserId)
		console.log("postAnonymous=" + this.postAnonymous)
	},
	data() {
		return {
			reply: {},
			sendVisible: false,
			replyId: 0,
			top: 0,
			currentReplyId: 0,
			postId: 0,
			postAnonymous: 0,
			postUserId: 0,
			isPostUserId: false,
			loginVisible: false
		}
	}
}
